.app {
  text-align: center;
  width: 500px;
  /* Height is controlled dynamically through App.js */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.1); /* More transparent background */
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(1px); /* Lighter blur */
  /* Additional styles to make the app more "invisible" */
  -webkit-app-region: drag; /* Makes the entire app draggable */
  user-select: none; /* Prevents text selection */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  z-index: 1; /* Lower z-index than settings */
  pointer-events: none; /* Make entire app click-through by default */
  min-height: 250px; /* Ensure consistent minimum height */
}

.app-header {
  margin-bottom: 20px;
  width: 100%;
}

.app-header h1 {
  margin: 0 0 10px 0;
  font-size: 24px;
}

.app-content {
  width: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  height: calc(100% - 40px); /* Adjust for header height */
  min-height: 180px; /* Ensure minimum content height */
}

.api-status {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 8px;
  font-size: 14px;
  color: #ccc;
  width: 100%;
  margin-bottom: 15px;
}

.status-indicator {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: rgba(255, 100, 100, 0.7);
}

.status-indicator.active {
  background-color: rgba(100, 255, 100, 0.7);
}

.settings-button {
  background-color: rgba(100, 100, 255, 0.7);
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 500;
  -webkit-app-region: no-drag; /* Make button clickable */
  pointer-events: auto !important; /* Override the app's pointer-events: none */
  position: relative; /* Ensure z-index works */
  z-index: 10; /* Keep button above background */
}

.settings-button:hover {
  background-color: rgba(100, 100, 255, 0.9);
}

.app-main-content {
  width: 100%;
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.click-through-indicator {
  display: inline-block;
  margin-left: 10px;
  font-size: 0.8rem;
  color: #4CAF50;
  background-color: rgba(76, 175, 80, 0.1);
  border: 1px solid rgba(76, 175, 80, 0.5);
  border-radius: 4px;
  padding: 2px 8px;
}
