.settings-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.2); /* Slightly visible background */
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100000; /* Very high z-index to be above everything */
  pointer-events: auto !important; /* Force pointer events to be active */
}

.settings-container {
  background-color: rgba(30, 30, 30, 0.97); /* Almost fully opaque for better visibility */
  border-radius: 8px;
  width: 90%;
  max-width: 360px;
  max-height: 90vh;
  overflow-y: auto;
  color: #fff;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.5);
  border: 2px solid rgba(255, 255, 255, 0.3);
  z-index: 100001; /* Higher z-index than the overlay */
  pointer-events: auto !important; /* Make sure pointer events work */
  backdrop-filter: blur(10px); /* Add stronger blur effect */
  position: relative; /* Needed for z-index to work properly */
  transform: translateZ(0); /* Force hardware acceleration */
  will-change: transform; /* Hint to browser about animations */
}

.settings-container * {
  pointer-events: auto !important;
}

.settings-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  background-color: rgba(50, 50, 50, 0.7); /* Darker header for better visibility */
  -webkit-app-region: drag; /* Allow dragging from the header */
  cursor: move; /* Show move cursor when hovering */
  border-radius: 8px 8px 0 0; /* Round top corners to match container */
}

.settings-header h2 {
  margin: 0;
  font-size: 18px;
  pointer-events: none; /* Prevents interference with dragging */
}

.close-button {
  background: rgba(200, 60, 60, 0.9);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: #fff;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  padding: 5px 12px;
  line-height: 1;
  z-index: 100002; /* Ensure close button is clickable */
  position: relative;
  pointer-events: auto;
  border-radius: 4px;
  -webkit-app-region: no-drag; /* Ensure button is clickable */
  transition: all 0.2s ease;
}

.close-button:hover {
  color: #fff;
  background: rgba(200, 50, 50, 0.9);
}

.close-button:active {
  background: rgba(255, 50, 50, 1);
  transform: scale(0.95);
}

.settings-content {
  padding: 20px;
}

.setting-group {
  margin-bottom: 20px;
}

.setting-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: #ccc;
}

.setting-group input {
  width: 100%;
  padding: 10px;
  font-size: 14px;
  background-color: rgba(0, 0, 0, 0.3); /* Darker input fields for better visibility */
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  color: #fff;
  margin-bottom: 5px;
  position: relative;
  z-index: 100002;
  pointer-events: auto;
}

.setting-group input:focus {
  outline: none;
  border-color: rgba(100, 100, 255, 0.5);
}

.setting-description {
  font-size: 12px;
  color: #999;
  margin: 5px 0 0 0;
}

.settings-actions {
  display: flex;
  justify-content: flex-end;
  margin-top: 15px;
}

.save-button {
  background-color: rgba(100, 100, 255, 0.7); /* More opaque button */
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 500;
  position: relative;
  z-index: 100002;
  pointer-events: auto;
  -webkit-app-region: no-drag; /* Ensure button is clickable */
}

.save-button:hover {
  background-color: rgba(100, 100, 255, 0.9);
}

.save-button:disabled {
  background-color: rgba(100, 100, 255, 0.3);
  cursor: not-allowed;
}

/* Styles for shortcuts section */
.shortcuts-container {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  padding: 10px;
  margin-bottom: 10px;
}

.shortcut-item {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
  padding: 5px 0;
}

.shortcut-item:last-child {
  margin-bottom: 0;
}

.shortcut-key {
  background-color: rgba(60, 60, 60, 0.8);
  color: #fff;
  padding: 4px 8px;
  border-radius: 4px;
  font-family: monospace;
  font-size: 12px;
  min-width: 60px;
  text-align: center;
  margin-right: 10px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.shortcut-description {
  color: #ccc;
  font-size: 13px;
  flex: 1;
}

/* Toggle switch styles */
.toggle-option {
  margin-bottom: 20px;
}

.toggle-wrapper {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 8px;
  border-radius: 4px;
  transition: background-color 0.2s;
  user-select: none;
}

.toggle-wrapper:hover {
  background-color: rgba(255, 255, 255, 0.05);
}

.toggle-switch {
  position: relative;
  width: 44px;
  height: 22px;
  background-color: rgba(60, 60, 60, 0.8);
  border-radius: 22px;
  transition: all 0.3s ease;
  margin-right: 12px;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.toggle-switch.active {
  background-color: rgba(76, 175, 80, 0.8);
  border-color: rgba(76, 175, 80, 0.5);
}

.toggle-knob {
  position: absolute;
  top: 2px;
  left: 2px;
  width: 16px;
  height: 16px;
  background-color: white;
  border-radius: 50%;
  transition: all 0.3s ease;
}

.toggle-switch.active .toggle-knob {
  left: 24px;
}

.toggle-label {
  font-weight: normal;
  color: #ccc;
}

/* Remove old checkbox styles */
.checkbox-container {
  display: none;
}
